:root {
  /* colors */
  --color-bg: #111;
  --color-dimmed: #666666;
  --color-light: #eeeeee;
  --color-highlight: #9dff00;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #111;
  background-color: var(--color-bg);
}

body {
  padding: 2rem;
  font-family: "Raleway", sans-serif;
  line-height: 1;
  color: #eeeeee;
  color: var(--color-light);
}

